<template>
  <div>
    <base-dialog
      :show="!!error"
      title="An error occurred!"
      @close="handleError"
    >
      <p>{{ error }}</p>
    </base-dialog>
    <section>
      <base-card>
        <header>
          <h2>Requests Received</h2>
        </header>
        <base-spinner v-if="isLoading"></base-spinner>
        <ul v-else-if="hasRequests && !isLoading">
          <request-item
            v-for="req in receivedRequests"
            :key="req.id"
            v-bind="req"
          ></request-item>
        </ul>
        <h3 v-else>You haven't received any requests yet!</h3>
      </base-card>
    </section>
  </div>
</template>

<script>
import RequestItem from '../../components/requests/RequestItem.vue';
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      isLoading: false,
      error: null
    };
  },
  components: {
    RequestItem
  },
  computed: {
    ...mapGetters('requests', {
      receivedRequests: 'receivedRequests',
      hasRequests: 'hasRequests'
    })
  },
  created() {
    this.loadRequests();
  },
  methods: {
    ...mapActions('requests', {
      fetchRequests: 'fetchRequests'
    }),
    async loadRequests() {
      this.isLoading = true;
      try {
        await this.fetchRequests();
      } catch (error) {
        this.error = error.message || 'Something went wrong!';
        console.log('Error');
      }
      this.isLoading = false;
    },
    handleError() {
      this.error = null;
    }
  }
};
</script>

<style scoped>
header {
  text-align: center;
}

ul {
  list-style: none;
  margin: 2rem auto;
  padding: 0;
  max-width: 30rem;
}

h3 {
  text-align: center;
}
</style>
